import {useEffect, useRef, useState} from 'react';
import {motion, useAnimation, useInView} from 'framer-motion';

import PortfolioItem from './PortfolioItem/PortfolioItem';

import image1 from './data/1.jpg'
import image2 from './data/2.jpg'
import image3 from './data/3.jpg'
import image4 from './data/4.jpg'
import image5 from './data/5.jpg'
import image6 from './data/6.jpg'
import image7 from './data/7.jpg'
import image8 from './data/8.jpg'
import image9 from './data/9.jpg'
import image10 from './data/10.jpg'

import './Portfolio.scss';

const Portfolio = () => {

  const images = [
    {
      name: '1',
      image: image1
    },
    {
      name: '2',
      image: image2
    },
    {
      name: '3',
      image: image3
    },
    {
      name: '4',
      image: image4
    },
    {
      name: '5',
      image: image5
    },
    {
      name: '6',
      image: image6
    },
    {
      name: '7',
      image: image7
    },
    {
      name: '8',
      image: image8
    },
    {
      name: '9',
      image: image9
    },
    {
      name: '10',
      image: image10
    }
  ]

  const [widthAndHeight, setWidth] = useState(0);

  useEffect(() => {
    const elementWidth = document.querySelector('.main-portfolio__list').clientWidth;
    const width = (elementWidth - 30) / 5;

    setWidth(width);
  }, []);

  const list = {
    hidden: {
      opacity: 0,
      transition: {
        when: 'afterChildren'
      }
    },
    visible: {
      opacity: 1,
      transition: {
        when: 'beforeChildren',
        staggerChildren: 0.3,
      }
    }
  };

  const controls = useAnimation();
  const ref = useRef(null)
  const isInView = useInView(ref, { once: true });
  useEffect(() => {
    if (isInView) {
      console.log('yes');
      controls.start("visible");
    }
  }, [controls, isInView]);

  return (
    <motion.ul
      className="main-portfolio__list"
      initial="hidden"
      animate="visible"
      variants={list}
      ref={ref}
    >
      {images.map((image, index) => {
        return (
          <PortfolioItem
            key={index}
            name={image.name}
            image={image.image}
            width={widthAndHeight}
          />
        )
      })}
    </motion.ul>
  );
};

export default Portfolio;
