import {useNavigate} from 'react-router-dom';
import Layout from '../../HOC/Layout/Layout';

import AnimateBackground from '../../components/AnimateBackground/AnimateBackground';
import Title from '../../components/Title/Title';
import Greeting from '../../components/Greeting/Greeting';
import Button from '../../components/Button/Button';
import TagCloud from '../../components/TagCloud/TagCloud';
import ContactsSocialLinks from '../../components/ContactsSocialLinks/ContactsSocialLinks';
import Portfolio from '../../components/Portfolio/Portfolio';

import './Main.scss';

const Main = () => {

  const text = `
  Hi!
  My name is Yauhen!
  Frontend developer
  You are welcome
  on my site!
  `;

  const navigate = useNavigate();

  return (
    <>
      <div className="main">
        <section className="main-section">
          <AnimateBackground/>
          <div
            className="main-section__wrapper"
          >
            <Greeting
              text={text}
            />
          </div>
        </section>
        <section className="main-section">
          <Title className="main-section__title">About</Title>
          <div className="main-section__content">
            <div className="about-section">
              <div className="about-section__info">
                <p className="about-section__text">
                  Front-end разработчик с 2+ годами опыта. Занимаюсь созданием и
                  поддержкой SPA-приложений на React и Redux. Также есть опыт создания мобильных приложений на Ionic +
                  Vue. Умею работать как в команде, так и полностью самостоятельно.
                  Более подробно можно ознакомится по ссылке ниже.
                </p>
                <Button className="about-section__button" onClick={() => navigate('/about')}>See more</Button>
              </div>
              <div className="about-section__data">
                <TagCloud/>
              </div>
            </div>

          </div>
        </section>
        <section className="main-section">
          <Title className="main-section__title">Portfolio</Title>
          <div className="main-section__content">
            <p className='main-portfolio__text'>
              Вот некоторые примеры моих работ. Для того чтобы увидеть больше, пройдите по ссылке ниже.
            </p>
            <Portfolio/>
            <Button
              className='main-portfolio__button'
              onClick={() => navigate('/portfolio')}
            >
              Portfolio
            </Button>
          </div>

        </section>
        <section className="main-section">
          <Title className="main-section__title">Contacts me</Title>
          <div className="main-section__content">

            <div className="contacts-section">
              <p className="contacts-section__text">Lorem ipsum dolor sit amet, consectetur adipisicing elit. Ab aliquid
                aut commodi corporis delectus dicta
                distinctio ducimus earum et ex exercitationem facere fuga incidunt inventore labore laudantium magnam
                natus nobis nostrum obcaecati quae, qui quidem quisquam reiciendis repellat rerum sit suscipit tempore
                tenetur vel. Aliquam dolor dolore esse ipsam iusto optio ratione sequi voluptas voluptate. Ab adipisci
                at
                beatae commodi culpa dolore doloribus ea earum esse excepturi fuga fugit impedit ipsam, libero magni
                minus
                nam necessitatibus nemo, non nostrum odit recusandae rerum saepe sed sit! Amet assumenda autem
                consequatur
                deleniti dolore inventore libero magnam omnis provident. Eius explicabo nobis quis.</p>
              <Button
                className="contacts-section__button"
                onClick={() => navigate('/contacts')}
              >
                Contacts me
              </Button>
              <ContactsSocialLinks/>
            </div>
          </div>

        </section>
        <footer className="footer">
          <div className="footer-bottom">Идея, дизайн, разработка - всё сам.</div>
        </footer>
      </div>
    </>
  );
};

export default Layout(Main);
