import * as React from 'react';
import {useState} from 'react';
import {motion, AnimatePresence} from 'framer-motion';
import {wrap} from 'popmotion';
import image1 from '../Portfolio/data/1.jpg';
import image2 from '../Portfolio/data/2.jpg';
import image3 from '../Portfolio/data/3.jpg';
import image4 from '../Portfolio/data/4.jpg';
import image5 from '../Portfolio/data/5.jpg';
import image6 from '../Portfolio/data/6.jpg';
import image7 from '../Portfolio/data/7.jpg';
import image8 from '../Portfolio/data/8.jpg';
import image9 from '../Portfolio/data/9.jpg';
import image10 from '../Portfolio/data/10.jpg';

import './GaleryMobile.scss';

const variants = {
  enter: (direction) => {
    return {
      x: direction > 0 ? 1000 : -1000,
      opacity: 0
    };
  },
  center: {
    zIndex: 1,
    x: 0,
    opacity: 1
  },
  exit: (direction) => {
    return {
      zIndex: 0,
      x: direction < 0 ? 1000 : -1000,
      opacity: 0
    };
  }
};

/**
 * Experimenting with distilling swipe offset and velocity into a single variable, so the
 * less distance a user has swiped, the more velocity they need to register as a swipe.
 * Should accomodate longer swipes and short flicks without having binary checks on
 * just distance thresholds and velocity > 0.
 */
const swipeConfidenceThreshold = 10000;
const swipePower = (offset, velocity) => {
  return Math.abs(offset) * velocity;
};

export default () => {

  const images = [
    {
      name: '1',
      image: image1
    },
    {
      name: '2',
      image: image2
    },
    {
      name: '3',
      image: image3
    },
    {
      name: '4',
      image: image4
    },
    {
      name: '5',
      image: image5
    },
    {
      name: '6',
      image: image6
    },
    {
      name: '7',
      image: image7
    },
    {
      name: '8',
      image: image8
    },
    {
      name: '9',
      image: image9
    },
    {
      name: '10',
      image: image10
    }
  ];


  const [[page, direction], setPage] = useState([0, 0]);

  // We only have 3 images, but we paginate them absolutely (ie 1, 2, 3, 4, 5...) and
  // then wrap that within 0-2 to find our image ID in the array below. By passing an
  // absolute page index as the `motion` component's `key` prop, `AnimatePresence` will
  // detect it as an entirely new image. So you can infinitely paginate as few as 1 images.
  const imageIndex = wrap(0, images.length, page);

  const paginate = (newDirection) => {
    setPage([page + newDirection, newDirection]);
  };

  return (
    <>
      <AnimatePresence initial={false} custom={direction}>
        <div className="gallery-mobile__wrapper">

          <motion.img
            className="gallery-mobile__image"
            key={page}
            src={images[imageIndex].image}
            custom={direction}
            variants={variants}
            initial="enter"
            animate="center"
            exit="exit"
            transition={{
              x: {type: 'spring', stiffness: 300, damping: 30},
              opacity: {duration: 0.2}
            }}
            drag="x"
            dragConstraints={{left: 0, right: 0}}
            dragElastic={1}
            onDragEnd={(e, {offset, velocity}) => {
              const swipe = swipePower(offset.x, velocity.x);

              if (swipe < -swipeConfidenceThreshold) {
                paginate(1);
              } else if (swipe > swipeConfidenceThreshold) {
                paginate(-1);
              }
            }}
          />
          <div className="gallery-mobile__description">
            <p>Lorem ipsum dolor.</p>
            <p>{images[imageIndex].name}</p>
          </div>
        </div>

      </AnimatePresence>
      <div className="next" onClick={() => paginate(1)}>
        {'‣'}
      </div>
      <div className="prev" onClick={() => paginate(-1)}>
        {'‣'}
      </div>
    </>
  );
};
